/*Resetted labels*/
* {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -webkit-font-feature-settings: normal !important;
  font-feature-settings: normal !important;
}

a,
a:hover, a:focus {
  text-decoration: none;
  outline: none;
}

a img {
  border: none;
  outline: none;
}

input {
  outline: none;
}

img {
  max-width: 100%;
}

p {
  hyphens: none !important;
}

html {
  font-size: 15px;
}

body {
  padding: 0;
  margin: 0;
  font-family: "Open Sans", sans-serif !important;
  color: #001b49 !important;
}

.navbar {
  background: #fff;
  color: #fff;
  padding: 0.5rem 0;
  transition: all 0.3s ease;
}
.navbar .nav-container {
  max-width: 1140px;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.navbar .nav-container .logo {
  color: white;
}
.navbar .nav-container .logo img {
  width: 150px;
  height: auto;
}
.navbar #main-menu {
  height: auto;
  display: block;
  margin-left: auto;
  flex: 0 1 auto;
}
.navbar #main-menu ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
}
.navbar #main-menu ul li a {
  padding: 0.8rem;
  margin: 0.3rem;
  color: #001b49;
  font-size: 1.2rem;
  transition: all 0.3s ease;
  text-transform: capitalize;
}
.navbar #main-menu ul li a:hover {
  border-bottom: 4px solid #001b49;
}
.navbar #main-menu ul li.active a {
  border-bottom: 4px solid #001b49;
}

.info-stripe {
  padding: 0.1rem;
  color: #fff;
  background: #001b49;
  text-align: center;
}

.banner-home img {
  width: 100%;
  display: block;
}

.home-content {
  padding: 1.5rem;
}
.home-content h1 {
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 3rem;
  font-size: 2.5rem;
  background: -webkit-radial-gradient(circle, #0655dd 15%, #001b49 30%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.quienes-somos-content {
  padding: 1.5rem;
}
.quienes-somos-content h2 {
  font-size: 2.5rem;
}
.quienes-somos-content p {
  text-align: justify;
}

.preguntas-frecuentes-content {
  padding: 1.5rem;
  text-align: center;
}
.preguntas-frecuentes-content h2 {
  font-size: 2.5rem;
}
.preguntas-frecuentes-content ul {
  text-align: left;
}

.contact-content {
  padding: 1.5rem;
}
.contact-content .contact-form-group #contact-form {
  display: flex;
  flex-wrap: wrap;
}
.contact-content .contact-form-group #contact-form .form-control {
  flex: 0 1 50%;
  display: flex;
  flex-direction: column;
  padding: 1rem;
}
.contact-content .contact-form-group #contact-form .form-control label {
  margin-bottom: 0.4rem;
}
.contact-content .contact-form-group #contact-form .form-control label::after {
  content: " * ";
  color: red;
}
.contact-content .contact-form-group #contact-form .form-control input, .contact-content .contact-form-group #contact-form .form-control select {
  height: 3rem;
  padding: 1rem 1.5rem;
  background-color: #fbfbfb !important;
  border: 1px solid #6d6d6d;
  color: #001b49;
  opacity: 0.8;
  appearance: none;
}
.contact-content .contact-form-group #contact-form .form-control input::placeholder {
  color: #001b49;
  opacity: 0.8;
}
.contact-content .contact-form-group #contact-form .form-control select {
  background: transparent;
  background-image: url("data:image/svg+xml;utf8,<svg fill='darkblue' height='24' viewBox='0 0 24 24' width='24' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>");
  background-repeat: no-repeat;
  background-position-x: 98%;
  background-size: 45px;
}

.footer {
  text-align: center;
  background: #001b49;
  color: #fff;
  padding: 1rem;
}
.footer p {
  margin: 0;
}

.mb-none {
  margin-bottom: 0;
}

.padding-none {
  padding-left: 0;
  padding-right: 0;
}

.text-center {
  text-align: center;
}

.full {
  clear: both;
  width: 100%;
}

.p-l-none {
  padding-left: 0;
}

.p-r-none {
  padding-right: 0;
}

.p-lr-none {
  padding-left: 0;
  padding-right: 0;
}

.clr {
  clear: both;
  float: none;
}

.center {
  margin: auto;
}

.inline {
  display: inline-block;
  vertical-align: top;
}

.inline-bottom {
  display: inline-block;
  vertical-align: bottom;
}

.left {
  float: left;
}

.right {
  float: right;
}

.relative {
  position: relative;
}

.wrapper-main {
  max-width: 1170px;
}

.wrapper-main-lg {
  max-width: 1230px;
}

.wrapper-main-md {
  max-width: 1000px;
}

.wrapper-main-sm {
  max-width: 850px;
}

/*END Default styles*/